* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

html, body {
  height: 100%; }

ul, li {
  list-style: none; }

a {
  text-decoration: none; }

#root, .wrap, .container, #intro, #intro > div {
  width: 100%;
  height: 100%;
  overflow-x: hidden; }

#intro {
  background: url(/hg-portfolio/static/media/bg_intro.374a525b.png);
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  display: table;
  color: #ffffff; }
  #intro > div {
    display: table-cell;
    vertical-align: middle; }

#about {
  width: 100%; }
  #about > div {
    margin-top: 2%;
    width: 100%;
    height: auto; }
  #about #my-pic:hover {
    -webkit-animation: spinMyPic 0.4s linear;
            animation: spinMyPic 0.4s linear; }

#projects {
  width: 100%;
  margin-top: 5%; }
  #projects .project-item {
    width: 480px;
    height: 280px;
    margin: 1%;
    border-radius: 15px;
    background-size: cover;
    background-position: center;
    box-shadow: 5px 5px 5px #C4C4C4; }
  #projects .project-item:hover {
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    -webkit-transition: all .4s linear;
    transition: all .4s linear; }

#contact {
  width: 100%;
  margin: 5% 0 5% 0; }
  #contact .contact-btn:hover {
    cursor: pointer;
    border: 1.3px solid #55A3A1 !important; }

.project-detail-name {
  margin-top: 3%; }

#html-helper {
  width: 100%;
  height: 1000px; }

#hacker-mon {
  width: 100%;
  height: 800px; }

#eight-record {
  width: 100%;
  height: 800px; }

@-webkit-keyframes spinMyPic {
  0% {
    transform: rotate(0);
    -webkit-transform: rotate(0); }
  100% {
    transform: rotate(180deg);
    -webkit-transform: rotate(180deg); } }

@keyframes spinMyPic {
  0% {
    transform: rotate(0);
    -webkit-transform: rotate(0); }
  100% {
    transform: rotate(180deg);
    -webkit-transform: rotate(180deg); } }

